import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Api } from 'core/services/api.service';

class ReviewedBy {

  user_id: string;
  name: string
};

export class ApplyCompOff {

  date: string = '';
  summary: string = '';
};

export class CompOff {

  _id: string;
  name: string;
  user_id: string;
  date: string;
  summary: string;
  expiry_date: string;
  reviewed_by: ReviewedBy;
  comp_off_status: number;
}

@Injectable()
export class CompensatoryOffService extends Api {

  private _base_url: string = '/leaves/compoffs';

  constructor(http: HttpClient) {

    super(http);
  }

  apply(compOff: ApplyCompOff): Promise<Object> {

    return this.post(this._base_url, compOff)
      .then(response => response);
  }

  getCompOffs(query?: Map<string, any>): Promise<CompOff[]> {

    return this.get(this._base_url, query)
      .then(response => response['data'] as CompOff[]);
  }

  approve(comp_off_id: string): Promise<Object> {

    return this.put(this._base_url + '/approve/' + comp_off_id, {})
      .then(response => response);
  }

  reject(comp_off_id: string): Promise<Object> {

    return this.put(this._base_url + '/reject/' + comp_off_id, {})
      .then(response => response);
  }

  cancel(compoff_id: string): Promise<Object> {

    return this.delete(this._base_url + '/cancel/' + compoff_id)
      .then(response => response);
  }
}
