import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';

import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { ApplyCompOff, CompensatoryOffService } from '../comp-off.service';
import { DatePipe } from '@angular/common';

const error_map = {
  date: 'Date',
  summary: 'Summary'
};

@Component({
  selector: 'app-apply-comp-off',
  templateUrl: './apply-comp-off.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [CompensatoryOffService, DatePipe]
})
export class ApplyCompOffComponent implements OnInit {
  @ViewChild('errorModal') errorModal: ErrorModalComponent;
  @ViewChild('successModal') successModal: SuccessModalComponent;

  compoff: ApplyCompOff;

  dateConfig: Object;

  error: string;
  invalid_attributes: string[];

  constructor(
    private compoffService: CompensatoryOffService,
    private datePipe: DatePipe
  ) {

    this.compoff = new ApplyCompOff();

    this.dateConfig = {
      dateInputFormat: 'YYYY-MM-DD',
      containerClass: 'theme-blue',
      showWeekNumbers: false,
      maxDate: new Date()
    };
  }

  async ngOnInit() {
  }

  private __validate() {
    return true;
  }

  private __sanitizeCompoff(compOff) {

    if (compOff.date !== undefined) {
      compOff.date = this.datePipe.transform(compOff.date, 'yyyy-MM-dd');
    }

    return compOff;
  }

  applyCompOff() {

    if (this.__validate()) {

      let compOff = JSON.parse(JSON.stringify(this.compoff));
      compOff = this.__sanitizeCompoff(compOff);
      this.error = '';
      this.invalid_attributes = [];

      this.compoffService.apply(compOff).then(data => {

        this.successModal.show('/leave/comp-off');

      }).catch(err => {

        this.error = err.error.message.text;
        if (err.error.invalid_attributes) {
          for (const key in err.error.invalid_attributes) {
            if (err.error.invalid_attributes.hasOwnProperty(key)) {
              this.invalid_attributes.push(error_map[key] ? error_map[key] : err.error.invalid_attributes[key]);
            }
          }
        }
        this.errorModal.show();
      });
    }
  }
}
