import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { STATUS } from 'config/constants/leave';
import { CompensatoryOffService, CompOff } from '../comp-off.service';

@Component({
  selector: 'app-review-comp-off',
  templateUrl: './review-comp-off.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [CompensatoryOffService]
})
export class ReviewCompOffComponent implements OnInit {

  compoffs: CompOff[] = [];

  reviewed: Array<Boolean>;
  approving: Array<Boolean>;
  rejecting: Array<Boolean>;

  constructor(
    private compoffService: CompensatoryOffService
  ) {

    this.reviewed = [];
    this.approving = [];
    this.rejecting = []
  }

  async ngOnInit() {

    this.loadCompOffs();
  }

  private loadCompOffs() {

    const self = this;
    const userId = localStorage.getItem('userId');
    const query = new Map<string, any>();
    query.set('approval_by', userId);
    query.set('comp_off_status', STATUS.APPLIED);

    this.compoffService.getCompOffs(query).then(compoffs => {
      self.compoffs = compoffs;
    });
  }

  approve(comp_off_id) {

    this.approving[comp_off_id] = true;
    this.compoffService.approve(comp_off_id).then(response => {

      this.reviewed[comp_off_id] = true;
      this.approving[comp_off_id] = false;
    });
  }

  reject(comp_off_id) {

    this.rejecting[comp_off_id] = true;
    this.compoffService.reject(comp_off_id).then(response => {

      this.reviewed[comp_off_id] = true;
      this.rejecting[comp_off_id] = false
    });
  }
}
